@import '__dropdown/header__dropdown.less';
.header {
  position: fixed;
  width: 100%;
  top: 0;
  padding-top: 30px;
  left: 0;
  z-index: 1000;
  -webkit-transition: bottom .5s ease;
  -o-transition: bottom .5s ease;
  transition: bottom .5s ease;
  @media (max-width: 1024px) {
    .header__logo .icon {
      font-size: 60px;
    }
  }
  @media (max-width: 768px) {
    .header__dropdown__selected {
      margin-top: 8px;
    }
    .header__tel {
      display: none;
    }
    &.header__white {
      background-color: #fff;
      .header__dropdown__selected {
        color: @dark;
        &_active {
          color: @red;
        }
        &:after {
          .svg-arrowDownBlack;
          .svg-arrowDownBlack-dims;
        }
      }
    }
    padding-top: 20px;
  }
  &_fix {
    background-color: #fff;
    top: 0;
    padding-top: 20px;
    height: 100px;
    border-bottom: solid 1px @light;
    .header__logo .icon {
      font-size: 60px;
    }
    .header__tel {
      margin-top: 10px;
      &__num {
        color: @dark;
      }
    }
    #chooseCity {
      display: none;
    }
    @media (max-width: 1280px) {
      .header__tel {
        margin-right: 60px;
      }
    }
    @media (max-width: 768px) {
      #chooseCity {
        display: block;
      }
      .header__dropdown__selected {
        color: @dark;
        &_active {
          color: @red;
        }
        &:after {
          .svg-arrowDownBlack;
          .svg-arrowDownBlack-dims;
        }
      }
    }
  }
  &__logo {
    display: block;
    width: 70px;
    height: 70px;
    .icon {
      font-size: 70px;
      -webkit-transition: font-size .2s ease;
      -o-transition: font-size .2s ease;
      transition: font-size .2s ease;
    }
  }
  &__tel {
    &:hover {
      .link_red{
        color: saturate(@red, 25%);
      }
    }
    &:active {
      .link_red{
        color: darken(@red, 15%);
      }
    }
    &__num {
      display: block;
      font-family: FedraSansAltProBold;
      font-size: 19px;
      line-height: 23px;
      margin-top: -4px;
      padding-bottom: 12px;
      &_white {
        color: #fff;
      }
    }
    span {
      font-family: FedraSansAltProMedTF;
      letter-spacing: -0.09px;
    }
  }
}