.main-block {
  padding-top: 197px;
  background-image: url(../images/backPicOne.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 760px;
  @media (max-width: 768px) {
    background-size: auto 480px;
    background-position: center top;
    padding-top: 430px;
    height: auto;
  }
  &__btn {
    margin-top: 40px;
    @media (max-width: 768px) {
      margin-left: auto;
      margin-right: auto;
      display: block;
      width: 100%;
      max-width: 345px;
    }
  }
  &__text {
    font-family: FedraSansAltProMedTF;
    margin-bottom: 111px;
    @media (max-width: 768px) {
      
    }
    &_white {
      color: #fff;
    }
    &_big {
      letter-spacing: -0.8px;
      line-height: 50px;
      margin-top: 10px;
      @media (max-width: 768px) {
        font-size: 24px;
        color: @dark;
        letter-spacing: -0.67px;
        line-height: 36px;
      }
    }
    &_small {
      margin-top: 19px;
      opacity: 0.5;
      letter-spacing: 0.5px;
      font-size: 11px;
      @media (max-width: 768px) {
        color: @darkGrey;
        font-size: 12px;
        letter-spacing: 0.55px;
      }
    }
  }
  .package-white {
    fill: #fff;
    font-size: 39px;
    @media (max-width: 768px) {
      font-size: 34px;
    }

  }
}