.nav {
  .header__tel {
    display: none;
  }
  &__trigger {
    display: none;
    width: 28px;
    height: 21px;
    border-top: solid 2px #000;
    position: relative;
    float: right;
    margin-right: 0px;
    margin-top: -68px;
    cursor: pointer;
    &:before {
      top: 9px;
    }
    &:after {
      top: 20px;
    }
    &:before,
    &:after {
      -webkit-transition: transform .2s ease;
      -o-transition: transform .2s ease;
      transition: transform .2s ease;
      content: '';
      position: absolute;
      left: 0;
      height: 2px;
      background-color: #000;
      width: 28px;
    }
    @media (max-width: 768px) {
      display: block;
      border-color: #fff;
      &:after,
      &:before {
        background-color: #fff;
      }
    }
    &_open {
      border: none;
      &:before {
        -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        transform: rotate(45deg);
        -webkit-transform-origin: left top;
        -moz-transform-origin: left top;
        -ms-transform-origin: left top;
        -o-transform-origin: left top;
        transform-origin: left top;
        top: 0;
      }
      &:after {
        -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
        -o-transform: rotate(-45deg);
        transform: rotate(-45deg);
        -webkit-transform-origin: left top;
        -moz-transform-origin: left top;
        -ms-transform-origin: left top;
        -o-transform-origin: left top;
        transform-origin: left top;
      }
      @media (max-width: 768px) {
        &:after,
        &:before {
          background-color: #000;
        }
      }
    }
  }
  &.nav_fix {
    height: 115px;
    left: 50%;
    margin-left: -670px;
    position: fixed;
    top: 22px;
    width: 1230px;
    z-index: 1001;
    @media(max-width: 1590px) {
      width: 1075px;
      margin-left: -560px;
    }
    @media(max-width: 1440px) {
      width: 900px;
      margin-left: -450px;
    }
    @media(max-width: 1280px) {
      width: 1024px;
      margin-left: -512px;
      top: 99px;
      background-color: #fff;
      height: auto;
      .nav__trigger {
        display: block;
      }
      ul {
        padding-top: 122px;
        padding-left: 210px;
        display: none;
      }
      .nav__item {
        float: none;
        width: 100%;
        border-bottom: none;
        display: block;
      }
    }
    @media(max-width: 1024px) {
      width: 100%;
      margin-left: 0;
      left: 0;
    }
    ul {
      width: 100%;
    }
    a {
      color: @dark;
    }
    @media (max-width: 768px) {
      top: 89px;
      ul {
        padding-top: 58px;
        padding-left: 0;
      }
      .nav__trigger {
        border-color: #000;
        &:after,
        &:before {
          background-color: #000;
        }
      }
    }
  }
  @media (max-width: 768px) {
    position: fixed;
    top: 89px;
    z-index: 1001;
    background-color: #fff;
    height: auto;
    ul {
      padding-top: 58px;
      padding-bottom: 30px;
      // padding-left: 30px;
      display: none;
      a {
        color: @dark;
      }
    }
    .nav__item {
      float: none;
      width: 100%;
      border-bottom: none;
      display: block;
      font-size: 24px;
    }
    .header__tel {
      margin-top: 45px;
      display: block;
    }
  }
  a{
    color: #fff;
    text-decoration: none;
  }
  &__item {
    width: 20%;
    float: left;
    font-family: FedraSansAltProMedTF;
    font-size: 15px;
    letter-spacing: -0.21px;
    line-height: 20px;
    height: 78px;
    // padding-bottom: 28px;
    a {
      display: block;
    }
    &_active,
    &:hover {
      border-bottom: solid 2px @orange;
    }
    &_small {
      font-family: FedraSansAltProBookTF;
      font-size: 12px;
      letter-spacing: -0.17px;
      line-height: 28px;
      display: block;
    }
  }
  @media (min-width: 1281px) {
    ul {
      display: block !important;
    }
  }
}