.price {
  margin-top: 46px;
  position: relative;
  background: transparent;
  text-align: center;
  @media(max-width: 1440px) {
    margin-top: 60px;
    text-align: left;
  }
  &__container {
    width: 1340px;
    margin: 0 auto;
    padding-left: 30px;
    padding-right: 30px;
    @media(max-width: 1440px) {
      width: 1280px;
    }
    @media(max-width: 1280px) {
      width: 1024px;
    }
    @media(max-width: 1024px) {
      width: 100%;
    }
    @media(max-width: 768px) {
      padding-left: 0;
      padding-right: 0;
    }
  }
  &:before {
    content: '';
    position: absolute;
    background-image: -webkit-linear-gradient(#FFFFFF 0%, #F4F4F4 100%);
    background-image: -o-linear-gradient(#FFFFFF 0%, #F4F4F4 100%);
    background-image: linear-gradient(#FFFFFF 0%, #F4F4F4 100%);
    width: 100%;
    height: 300px;
    bottom: -109px;
    left: 0;
    z-index: 1;
  }
  &__title {
    line-height: 71px;
    @media(max-width: 1440px) {
      width: 440px;
      margin:0 auto 17px;
      letter-spacing: -0.94px;
      line-height: 42px;
    }
  }
  &__list {
    margin-top: 71px;
    &_xs {
      @media(max-width: 1440px) {
        display: none;
      }
    }
    &_md {
      display: none;
      @media(max-width: 1440px) and (min-width: 769px) {
        display: block;
        margin-top: 104px;
        .price__btn_tb {
          font-size: 14px;
          padding: 15px 53px 11px;
          margin-top: 22px;
        }
        .price__list__item.price__list__item_default {
          box-shadow: none;
          border-left: solid 1px @light;
          border-right: solid 1px @light;
          border-bottom: solid 1px @light;
          padding: 27px 25px 19px 33px;
        }
        .price__list__item.price__list__item_special{
          padding: 27px 25px 25px 33px;
        }
        .price__size__wrap {
          float: left;
          width: 28.5%;
        }
        .price__size {
          font-size: 24px;
          margin-bottom: 12px;
        }
        .price__size__name {
          font-size: 13px;
          font-family: FedraSansAltProBookTF;
          font-size: 13px;
          letter-spacing: 0.2px;
          line-height: 18px;
        }
        .price__cost_wrap {
          float: right;
        }
        .price__list__item_special .price__cost,
        .price__list__item_default .price__cost {
          margin-top: -7px;
          font-family: FedraSansAltProMedTF;
          font-size: 48px;
          letter-spacing: -0.48px;
          height: 58px;
          &__name {
            margin-right: 0;
            margin-left: 16px;
            text-align: center;
            &_up,
            &_down {
              font-size: 12px;
            }
          }
        }
        .price__list__item_special .price__profit,
        .price__list__item_default .price__profit {
          margin-top: 0;
          font-size: 12px;
          letter-spacing: 0.1px;
        }
        .price__descript {
          float: left;
        }
        .price__list__item_special .price__descript,
        .price__list__item_default .price__descript {
          margin-top: 0;
          font-size: 14px;
          width: 40%;
          .price__descript_wrap {
            width: 125px;
          }
        }
      }
      @media(max-width: 768px) {
        display: block;
        border-top: solid 1px @light;
        .price__list__item:nth-child(2n) {
          background-color: @lLight;
        }
        .price__list__item.price__list__item_special,
        .price__list__item.price__list__item_default {
          margin-top: 0;
          box-shadow: none;
          padding: 30px 20px;
        }
        .price__size__wrap {
          float: left;
        }
        .price__list__item_special .price__size,
        .price__list__item_default .price__size {
          font-family: FedraSansAltProBold;
          font-size: 24px;
          letter-spacing: 0;
          line-height: 22px;
          margin-bottom: 8px;
        }
        .price__size__name {
          font-family: FedraSansAltProBookTF;
          font-size: 13px;
          letter-spacing: 0.2px;
          line-height: 18px;
        }
        .price__cost_wrap {
          float: right;
        }
        .price__list__item_special .price__cost,
        .price__list__item_default .price__cost {
          margin-top: 0;
          margin-left: 0;
          font-size: 48px;
          letter-spacing: -0.48px;
          line-height: 64px;
        }
        .price__list__item_special .price__cost__name,
        .price__list__item_default .price__cost__name {
          margin-right: 0;
        }
      }
    }
    &__item {
      position: relative;
      background: #FFFFFF;
      box-shadow: 0 0 8px 0 rgba(0,0,0,0.10), 0 6px 8px 0 rgba(0,0,0,0.06), 0 2px 13px 0 rgba(0,0,0,0.05), 0 2px 4px 0 rgba(0,0,0,0.10);
      border-radius: 1px;
      float: left;
      &_default {
        padding-top: 46px;
        width: 310px;
        z-index: 2;
        @media(max-width: 1440px) {
          width: 100%;
        }
        .price {
          &__cost {
            margin-top: 91px;
            color: @dark;
            font-size: 80px;
            letter-spacing: -0.8px;
            line-height: 64px;
            &:not(.price__cost_xl) {
              margin-left: 56px;
            }
            &__name {
              margin-right: 35px;
              &_up {
                border-color: @dark;
              }
            }
          }
          &__profit {
            margin-top: 21px;
          }
          &__descript {
            margin-top: 60px;
          }
          &__buy {
            margin-top: 98px;
          }
        }
      }
      &_special {
        padding-top: 70px;
        width: 350px;
        margin-top: -30px;
        z-index: 3;
        position: relative;
        @media(max-width: 1440px) {
          width: 100%;
        }
        .price {
          &__cost {
            margin-top: 93px;
            margin-left: 59px;
            color: @red;
            font-size: 90px;
            letter-spacing: -0.9px;
            line-height: 64px;
            &__name {
              margin-right: 59px;
              &_up {
                border-color: @red;
              }
            }
          }
          &__size {
            color: @dark;
          }
          &__profit {
            margin-top: 16px;
          }
          &__descript {
            margin-top: 78px;
          }
          &__buy {
            margin-top: 51px;
            padding: 20px 0 19px;
          }
        }
      }
    }
  }
  &__size {
    font-family: FedraSansAltProBold;
    font-size: 30px;
    color: @lightGrey;
    letter-spacing: 0;
    line-height: 22px;
    margin-bottom: 18px;
    &__name {
      font-family: FedraSansAltProMedTF;
      font-size: 14px;
      color: @darkGrey;
      letter-spacing: -0.22px;
      line-height: 18px;
    }
  }
  &__cost {
    font-family: FedraSansAltProMedTF;
    &_xl {
      margin-left: 15px;
    }
    &__name {
      font-family: FedraSansAltProMedTF;
      letter-spacing: 0;
      line-height: 24px;
      width: 40px;
      &_up {
        border-bottom: solid 1px;
        padding-bottom: 5px;
        font-size: 13px;
      }
      &_down {
        font-size: 15px;
      }
    }
  }
  &__profit {
    font-family: FedraSansAltProBookTF;
    font-size: 13px;
    color: @grey;
    letter-spacing: 0.11px;
    line-height: 18px;
  }
  &__descript {
    font-family: FedraSansAltProMedTF;
    font-size: 16px;
    color: @dark;
    letter-spacing: -0.11px;
    line-height: 20px;
    &_pull-down {
      margin-bottom: 19px;
    }
  }
  &__buy {
    border-top: solid 1px @light;
  }
}